import React from 'react'
import {observer} from 'mobx-react-lite';
import CustomCard from "../../CustomCard/CustomCard";
import {nanoid} from "nanoid";
import './TaskList.scss'
import taskScheduleStore from "../../../../lib/store/task-schedule-store";
import {FilterVal} from "../../../../lib/helper";
import {Category} from "../../../../lib/models/category";


interface Interface {
    data: Category[]
}

export default observer(({data}: Interface) => {


    return (
        <div className='task-list'>
            {
                FilterVal(data).map((data) => (
                    data.folder === taskScheduleStore.folderName ?
                        <div key={nanoid()} className='task-list-card'>
                            <div key={nanoid()}
                                 className='task-list-card-date'> {taskScheduleStore.nowDate === data.date ? 'Сегодня' : data.date}</div>
                            {
                                <CustomCard key={nanoid()} data={data}/>
                            }
                        </div>
                        :
                        null
                ))
            }
        </div>
    )
})
