import React, {useState} from 'react'
import {observer} from 'mobx-react-lite'
import './HeaderApp.scss'
import {nanoid} from "nanoid";
import {dataBtnHeaderAppCategory, dataBtnHeaderAppFolder} from "../../../Data/DataScheduleTask";
import taskScheduleStore from "../../../app/lib/store/task-schedule-store";
import taskContentStore from "../../../app/lib/store/task-content-store";
// @ts-ignore
import exit from '../../../app/components/general/Imag/exit.svg'

export const HeaderApp: React.FC = observer(({children}) => {
    const [stateFolder, setStateFolder] = useState(0)


    const handelChangeFolderName = (val: number): void => {
        setStateFolder(val)
        taskScheduleStore.setFolderName(val)
        taskScheduleStore.setStateTaskContent(false)
        taskContentStore.setDate('')
    }
    const handelChangeStateCategory = (val: number): void => {
        taskScheduleStore.setStateBtnCategory(val)
        taskScheduleStore.setStateTaskContent(false)
        taskContentStore.setDate('')
    }
    return (
          <div key={nanoid()} className="drawer drawer-mobile">
              <input key={nanoid()} id="my-drawer-2" type="checkbox" className="drawer-toggle"/>
              <div key={nanoid()} className="drawer-content bg-base-200 flex-col items-center justify-center">
                  {children}
              </div>
              <div key={nanoid()} className="drawer-side">
                  <label key={nanoid()} htmlFor="my-drawer-2" className="drawer-overlay"/>
                  <ul key={nanoid()} className="menu p-6  bg-base-100 text-base-content">
                      <div key={nanoid()} className={'header-app-title'}>
                          <label key={nanoid()} htmlFor="my-modal-exit" aria-current="page" aria-label="Homepage"
                             className="flex-0 btn btn-ghost btn-authorization">
                              Левченко Е.А <img alt='exit' src={exit}/>
                          </label>
                      </div>
                      <div>
                          {
                              dataBtnHeaderAppFolder.map((data: any) => (
                                  <div key={nanoid()}>
                                      <p className="drawer-side-category-title" key={nanoid()}>{data.folder_name}</p>

                                      {
                                          data.folder_list.map((list: any, index: number) => (
                                              <li key={nanoid()}>
                                                  <a href={'/#'} onClick={() => handelChangeFolderName(list.value)} key={nanoid()}
                                                     className={`btn ${stateFolder === index && 'btn-active'} no-animation btn-ghost btn-list`}>
                                                      {list.title}
                                                      <div key={nanoid()} style={{background: list.colorCircle}}
                                                           className={'circle'}/>
                                                  </a>
                                              </li>
                                          ))
                                      }
                                  </div>
                              ))
                          }
                      </div>
                      <br/>
                      <br/>
                      <div>
                          {
                              dataBtnHeaderAppCategory.map((data: any) => (
                                  <div key={nanoid()}>
                                      <p className="drawer-side-category-title" key={nanoid()}>{data.category_name}</p>
                                      <li key={nanoid()}>
                                          {
                                              data.category_list.map((list: any, index: number) => (
                                                  <a onClick={() => handelChangeStateCategory(list.value)} key={nanoid()}
                                                     href={list.link}
                                                     className={`btn-list btn ${taskScheduleStore.stateBtnCategory === index && 'btn-active'} no-animation btn-ghost `}>
                                                      {list.title}
                                                      <div style={{background: list.colorCircle}} className={'circle'}/>
                                                  </a>
                                              ))
                                          }
                                      </li>
                                  </div>
                              ))
                          }
                      </div>
                  </ul>
              </div>
          </div>
    )
})
