import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import taskScheduleStore from "../../../../lib/store/task-schedule-store";
// @ts-ignore
import plus from '../../Imag/plus.svg'
import {changeStateTaskContent} from "../../../../lib/helper";

interface Interface {
    onChange: any
    val: any
}

export default observer(({val}: Interface) => {
    const [newArray, setNewArray] = useState({})

    const addArray = () => {
        setNewArray({
            folder: 0,//Запланированные
            category: taskScheduleStore.stateBtnCategory,
            id: 0,
            date: taskScheduleStore.nowDate,
            task_title: 'Новая задача',
            task_body: ''
        })

            changeStateTaskContent({
                folder: 0,//Запланированные
                category: taskScheduleStore.stateBtnCategory,
                id: 0,
                date: taskScheduleStore.nowDate,
                task_title: 'Новая задача',
                task_body: ''
            })



    }
    useEffect(() => {
        val.push(newArray)
    }, [newArray])
    return (

        <a href={'/#'} onClick={addArray} className="btn btn-warning home-search-form-btn">
            <img alt='plus' src={plus}/>
        </a>


    )
})
