import React, {useEffect} from 'react';
import {observer} from 'mobx-react-lite'
import './App.scss';
import {HeaderApp} from '../../../core/components/HeaderApp/HeaderApp';
import {nanoid} from "nanoid";
import Router from "../Router/Router";
import {toNowDay} from "../../../core/date";
import taskScheduleStore from "../../lib/store/task-schedule-store";
import {dataCategoryHome, dataCategoryJob, dataCategoryPersonal,} from "../../../Data/DataScheduleTask";
import ModalDelete from "../general/CustomModal/ModalDelete/ModalDelete";
import ModalExit from "../general/CustomModal/ModalExit/ModalExit";
import FormAuthReg from "../Pages/FormAuthReg/FormAuthReg";


export default observer(() => {


    useEffect(() => {
        taskScheduleStore.setNowDate(toNowDay())
        taskScheduleStore.setDataCategoryHome(dataCategoryHome)
        taskScheduleStore.setDataCategoryJob(dataCategoryJob)
        taskScheduleStore.setDataCategoryPersonal(dataCategoryPersonal)
        const all = [...dataCategoryPersonal, ...dataCategoryHome, ...dataCategoryJob]
        taskScheduleStore.setDataCategoryAll(all)
    }, [])

    return (
        <div>
            <ModalDelete/>
            <ModalExit/>
            {
                taskScheduleStore.auth ?
                    <HeaderApp key={nanoid()}>
                        <div key={nanoid()} className='app'>
                            <Router/>
                        </div>
                    </HeaderApp>
                    :
                    <FormAuthReg/>
            }
        </div>

    );
})


