import React, {useState} from 'react'
import {observer} from 'mobx-react-lite'
import Calendar from "react-calendar";
import './MyCalendar.scss'
import 'react-calendar/dist/Calendar.css';
// @ts-ignore
import iconCalendar from '../Imag/calendar.svg'
import taskContentStore from "../../../lib/store/task-content-store";
import {toDateBackend} from "../../../../core/date";

export default observer(() => {
    const [calendar, setCalendar] = useState(new Date());
    const onChangeCalendar = (e: any) => {
        taskContentStore.setDate(toDateBackend(e))
        setCalendar(e)
    }
    return (
        <div className='task-content'>

            <div className="dropdown dropdown-bottom dropdown-end">
                <label tabIndex={1} className="btn-calendar btn btn-ghost m-2">
                    <img alt='calendar' src={iconCalendar}/>
                </label>
                <div tabIndex={1} className="dropdown-content card card-compact w-64 p-2 shadow ">
                    <Calendar next2Label={false} prev2Label={false} onChange={onChangeCalendar} value={calendar}/>
                </div>
            </div>

        </div>
    )
})
