import {makeAutoObservable} from 'mobx'


interface ITaskContentStore {
    date: string
    taskBody: string
    taskTitle: string
    taskCategory: number
    checkbox:boolean
}

class TaskContentStore implements ITaskContentStore {
    date = ''
    taskBody = ''
    taskTitle = ''
    taskCategory = 0
    checkbox = false

    constructor() {
        makeAutoObservable(this)
    }

    setDate(val: string): void {
        this.date = val
    }

    setTaskBody(val: string): void {
        this.taskBody = val
    }

    setTaskTitle(val: string): void {
        this.taskTitle = val
    }
    setTaskCategory(val: number): void {
        this.taskCategory = val
    }
    setCheckbox(val: boolean): void {
        this.checkbox = val
    }

}


export default new TaskContentStore()
