import React, {ChangeEvent, useState} from 'react'
import {observer} from 'mobx-react-lite'
import taskScheduleStore from "../../../../lib/store/task-schedule-store";
import formAuthRegStore from "../../../../lib/store/form-auth-reg-store";
import {validateEmail} from "../../../../lib/helper";


export default observer(() => {
    const [stateErrorEmail, setStateErrorEmail] = useState(false)
    const [stateErrorConfirm, setStateErrorConfirm] = useState(false)
    const enter = () => {
        if (stateErrorEmail && stateErrorEmail) {
            console.log(1)
        } else {
            console.log(2)
        }
    }
    const changeStateFormAuth = () => {
        taskScheduleStore.setStateFormAuth(true)
    }
    const onBlurEmail = () => {
        if (!validateEmail(formAuthRegStore.regEmail)) {
            setStateErrorEmail(true)
        } else {
            setStateErrorEmail(false)
        }
    }
    const onBlurPass = () => {
        if (formAuthRegStore.regPass !== formAuthRegStore.regPassConfirm) {
            setStateErrorConfirm(true)
        } else {
            setStateErrorConfirm(false)
        }
    }

    return (

        <div className='authorization-form'>
            {
                stateErrorEmail || stateErrorConfirm ?
                    <div className="toast toast-top toast-end">
                        <div className="alert alert-error">
                            <div>
                                <svg xmlns="http://www.w3.org/2000/svg" className="stroke-current flex-shrink-0 h-6 w-6"
                                     fill="none" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2"
                                          d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"/>
                                </svg>
                                <span>Некорректный ввод данных</span>
                            </div>
                        </div>
                    </div>
                    :
                    null
            }
            <div className='authorization-form-input'>
                <div>
                    Регистрация
                </div>
                <br/>
                <input onBlur={onBlurEmail} value={formAuthRegStore.regEmail}
                       onChange={(event) => formAuthRegStore.setRegEmail(event.target.value)} type="text"
                       placeholder="Электронная почта"
                       className={`input ${stateErrorEmail ? 'input-error' : ''} authorization-form-input-item w-full`}/>
                <input value={formAuthRegStore.regPass}
                       onChange={(event) => formAuthRegStore.setRegPass(event.target.value)} type="password"
                       placeholder="Пароль" className="input authorization-form-input-item w-full"/>
                <input onBlur={onBlurPass} value={formAuthRegStore.regPassConfirm}
                       onChange={(event) => formAuthRegStore.setRegPassConfirm(event.target.value)} type="password"
                       placeholder="Подтверждение пароля"
                       className={`input ${stateErrorConfirm ? 'input-error' : ''} authorization-form-input-item w-full`}/>
            </div>
            <div className='authorization-form-btn w-full'>
                <button disabled={stateErrorEmail || stateErrorConfirm} onClick={enter}
                        className="btn btn-secondary authorization-form-btn-item ">Зарегистрироваться
                </button>
                <button onClick={changeStateFormAuth}
                        className="btn btn-outline btn-secondary authorization-form-btn-item">Авторизоваться
                </button>
            </div>
            <br/>
        </div>

    )
})
