import moment from 'moment'
import 'moment/locale/ru'


export const formatDateToPicker = 'dd.MM.yyyy'
const formatDateToBackend = 'DD.MM.YYYY'
const formatDatePicker = 'DD-MM-YYYY'

export const toDateBackend = (value: string): string => {
    // eslint-disable-next-line @typescript-eslint/no-unsafe-call
    return value ? moment(value).format(formatDateToBackend) : ''
}

export const toDatePicker = (value: Date): string => {
    // @ts-ignore
    return value ? moment(value, formatDatePicker, 'ru').format('L') : null
}
export const toNowDay = () => {
    return moment().format('DD.MM.YYYY')
}
export const toWeekPicker = (value: string): number => {
    // @ts-ignore
    return value ? moment(value, formatDatePicker, 'ru').week() : null
}
export const toDayPicker = (value: string): string => {
    // @ts-ignore
    return value ? moment(value, formatDatePicker, 'ru').format('DD') : null
}
export const toMonthPicker = (value: string): string => {
    // @ts-ignore
    return value ? moment(value, formatDatePicker, 'ru').format('MMMM') : null
}
export const toMonthPickerSmall = (value: string): string => {
    // @ts-ignore
    return value ? moment(value, formatDatePicker, 'ru').format('MMM') : null
}
export const toYearPicker = (value: string): string => {
    // @ts-ignore
    return value ? moment(value, formatDatePicker, 'ru').format('YYYY') : null
}

export default {
    toDatePicker,
    toWeekPicker,
    toDayPicker,
    toDateBackend,
    toMonthPicker,
    toYearPicker,
    toMonthPickerSmall,
}
