import {makeAutoObservable} from 'mobx'
import {Category} from "../models/category";


interface ITaskScheduleStore {
    nowDate: string
    stateTaskContent: boolean
    dataCategoryHome: Category[]
    dataCategoryJob: Category[]
    dataCategoryPersonal: Category[]
    dataCategoryAll: Category[]
    folderName: number
    stateBtnCategory: number
    valCalendar: string
    stateModal:boolean
    auth:boolean
    stateFormAuth:boolean
}

class TaskScheduleStore implements ITaskScheduleStore {
    nowDate = ''
    stateTaskContent = false
    dataCategoryHome: Category[] = []
    dataCategoryJob: Category[] = []
    dataCategoryPersonal: Category[] = []
    dataCategoryAll: Category[] = []
    folderName = 0
    stateBtnCategory = 1
    valCalendar = ''
    stateModal = false
    auth = false
    stateFormAuth = true


    constructor() {
        makeAutoObservable(this)
    }

    setNowDate(val: string): void {
        this.nowDate = val
    }

    setStateTaskContent(val: boolean): void {
        this.stateTaskContent = val
    }

    setDataCategoryHome(val: Category[]): void {
        this.dataCategoryHome = val
    }

    setDataCategoryJob(val: Category[]): void {
        this.dataCategoryJob = val
    }

    setDataCategoryPersonal(val: Category[]): void {
        this.dataCategoryPersonal = val
    }

    setDataCategoryAll(val: Category[]): void {
        this.dataCategoryAll = val
    }

    setFolderName(val: number): void {
        this.folderName = val
    }

    setStateBtnCategory(val: number): void {
        this.stateBtnCategory = val
    }

    setValCalendar(val: string): void {
        this.valCalendar = val
    }
    setStateModal(val: boolean): void {
        this.stateModal = val
    }
    setAuth(val: boolean): void {
        this.auth = val
    }

    setStateFormAuth(val: boolean): void {
        this.stateFormAuth = val
    }

}

export default new TaskScheduleStore()
