import React, {ChangeEvent} from 'react'
import {observer} from 'mobx-react-lite'
import AddTask from "../AddTask/AddTask";
import searchFormStore from "../../../../lib/store/search-form-store";
import taskScheduleStore from "../../../../lib/store/task-schedule-store";
// @ts-ignore
import search from '../../Imag/search.svg'
interface Interface {
    onChange: any
    val: any

}

export default observer(({onChange, val}: Interface) => {

    const changeValueHandler = (event: ChangeEvent<HTMLInputElement>): void => {
        searchFormStore.setValue(event.target.value)
    }
    const onFocus = () => {
        taskScheduleStore.setStateTaskContent(false)
    }
    return (
        <form onFocus={onFocus} onBlur={onFocus} autoComplete={'off'} className="flex items-center w-full">
            <AddTask val={val} onChange={onChange}/>
            <label htmlFor="simple-search" className="sr-only">Поиск</label>
            <div className="relative w-full">
                <div className="absolute inset-y-0 left-0 flex items-center pl-3 pointer-events-none">
                   <img alt='search' src={search}/>
                </div>
                <input value={searchFormStore.value} onChange={changeValueHandler}
                       type="text" id="simple-search"
                       className="home-search-form-input  input w-full  pl-10  "
                       placeholder="Поиск"/>
            </div>
        </form>
    )
})
