import React from 'react'
import {observer} from 'mobx-react-lite';
import './ModalExit.scss'
import taskScheduleStore from "../../../../lib/store/task-schedule-store";
// @ts-ignore
import iconClose from '../../Imag/close.svg'

export default observer(() => {
    const changeAuth = () => {
        taskScheduleStore.setAuth(false)
    }
    return (
        <>
            <input type="checkbox" id="my-modal-exit" className="modal-toggle"/>
            <div className="modal">
                <div className="modal-box">
                    <label htmlFor="my-modal-exit" className="btn btn-ghost btn-sm  absolute right-2 top-2"><img
                        alt='close' src={iconClose}/></label>

                    <div className='modal-exit-box'>
                        <div className='modal-exit-box-text'>
                            Вы уверены, что хотите выйти?
                        </div>
                        <div className='modal-exit-box-btn'>
                            <label htmlFor="my-modal-exit" onClick={changeAuth}
                                   className="btn btn-secondary modal-exit-box-btn-item">Выйти</label>
                            <label htmlFor="my-modal-exit"
                                   className="btn btn-outline btn-secondary modal-exit-box-btn-item">Отменить</label>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
})
