import React, {useState} from 'react'
import {observer} from 'mobx-react-lite'
import taskScheduleStore from "../../../../lib/store/task-schedule-store";
import {validateEmail} from "../../../../lib/helper";
import formAuthRegStore from "../../../../lib/store/form-auth-reg-store";


export default observer(() => {

    const enter = () => {
        taskScheduleStore.setAuth(true)
    }
    const changeStateFormAuth = () => {
        taskScheduleStore.setStateFormAuth(false)
    }


    return (

        <div className='authorization-form'>
            <div className='authorization-form-input'>
                <div>
                    Войти
                </div>
                <br/>
                <input value={formAuthRegStore.authEmail}
                       onChange={(event) => formAuthRegStore.setAuthEmail(event.target.value)} type="text" placeholder="Электронная почта"
                       className="input authorization-form-input-item w-full"/>
                <input value={formAuthRegStore.authPass}
                       onChange={(event) => formAuthRegStore.setAuthPass(event.target.value)} type="password" placeholder="Пароль" className="input authorization-form-input-item w-full"/>
            </div>
            <div className='authorization-form-btn w-full'>
                <button onClick={enter} className="btn btn-secondary authorization-form-btn-item ">Войти</button>
                <button onClick={changeStateFormAuth}
                        className="btn btn-outline btn-secondary authorization-form-btn-item">Зарегистрироваться
                </button>
            </div>
            <br/>
        </div>

    )
})
