import React, {useState} from 'react'
import {observer} from 'mobx-react-lite';
import './CustomCard.scss'

import {changeStateTaskContent} from "../../../lib/helper";

interface Interface {
    data: any
}

export default observer(({data}: Interface) => {
    const [stateCheck, setStateCheck] = useState(false)


    return (
        <div
            style={data.category === 1 ?
                {background: '#F89705'}
                :
                data.category === 2 ?
                    {background: '#5496FB'}
                    :
                    data.category === 3 ?
                        {background: '#C262DA'} :
                        {background: '#ffffff'}
            }
            className="w-full  custom-card">
            <div className="w-full bg-base-100 custom-card-pre">
                <input onChange={() => setStateCheck(!stateCheck)} type='checkbox' checked={stateCheck}
                       className="checkbox checkbox-secondary"/>
                <div onClick={() => changeStateTaskContent(data)} className="custom-card-pre-text"
                     style={stateCheck ? {color: '#8F9399'} : {}}>
                    {data.task_title}
                </div>
            </div>
        </div>
    )
})
