import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {dataBtnHeaderAppCategory} from "../../../../../Data/DataScheduleTask";
import {nanoid} from "nanoid";
import taskContentStore from "../../../../lib/store/task-content-store";


export default observer(() => {
    const [state, setState] = useState('Без категории')
    const [color, setColor] = useState('')
    useEffect(() => {
        if (taskContentStore.taskCategory === 1) {
            setState('Дом')
            setColor('#F89705')
        } else if (taskContentStore.taskCategory === 2) {
            setState('Работа')
            setColor('#5496FB')
        } else if (taskContentStore.taskCategory === 3) {
            setState('Личное')
            setColor('#C262DA')
        }
    }, [])
    return (
        <div className="dropdown">
            <label tabIndex={0} className="btn btn-ghost m-1">
                {state}
                <div key={nanoid()} style={{background: `${color}`, marginLeft: '.5rem'}}
                     className={'circle'}/>
            </label>
            <ul tabIndex={0} className="dropdown-content menu p-2 shadow bg-base-100 rounded-box w-52">
                <li onClick={() => {
                    setState('Без категории')
                    setColor('')
                }} key={nanoid()}><a href={'/#'} key={nanoid()}> Без категории
                </a>
                </li>
                {
                    dataBtnHeaderAppCategory.map((data: any) => (

                        data.category_list.map((list: any, index: number) => (
                            index !== 0 ?
                                <li onClick={() => {
                                    setState(list.title)
                                    setColor(list.colorCircle)
                                }} key={nanoid()}><a href={'/#'}
                                                     key={nanoid()}>{list.title}
                                    <div key={nanoid()} style={{background: list.colorCircle}}
                                         className={'circle'}/>
                                </a>
                                </li>
                                :
                                null
                        ))

                    ))
                }
            </ul>
        </div>
    )
})
