import {HashRouter, Redirect, Route, Switch} from 'react-router-dom'
import React from 'react'

import {observer} from 'mobx-react-lite'
import routes from "../../lib/routes";
import CategoryAll from "../Pages/CategoryAll/CategoryAll";
import CategoryJob from "../Pages/CategoryJob/CategoryJob";
import CategoryPerson from "../Pages/CategoryPerson/CategoryPerson";
import CategoryHome from "../Pages/CategoryHome/CategoryHome";





const Router = observer(() => {
    return (
        <HashRouter basename={routes.HOME}>
            <Switch>
                <Route exact path={routes.HOME} component={CategoryHome}/>
                <Route exact path={routes.ALL} component={CategoryAll}/>
                <Route exact path={routes.JOB} component={CategoryJob}/>
                <Route exact path={routes.PERSONAL} component={CategoryPerson}/>

                <Redirect from="*" to={routes.HOME}/>
            </Switch>
        </HashRouter>
    )
})

export default Router
