import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import {nanoid} from "nanoid";
import {Category} from "../../../lib/models/category";
import taskScheduleStore from "../../../lib/store/task-schedule-store";
import SearchForm from "../Search/SearchForm/SearchForm";
import TaskList from "./TaskList/TaskList";
import TaskContent from "./TaskContent/TaskContent";
import './Skeleton.scss'
import {CSSTransition} from "react-transition-group";

interface Interface {
    props: Category[]

}

export default observer(({props}: Interface) => {
    const [data, setDate] = useState<Category[]>([])

    const onFocus = () => {
        taskScheduleStore.setStateTaskContent(false)
    }
    useEffect(() => {
        setDate(props)
    }, [props])
    const onChangeData = (val: Category[]): void => {
        setDate(val)
    }

    return (
        <div className={'home'}>
            <div
                tabIndex={0} onFocus={onFocus}
                className={!taskScheduleStore.stateTaskContent ? 'home-task-list-active' : 'home-task-list'}>
                <SearchForm val={data} onChange={onChangeData}/>
                <TaskList key={nanoid()} data={data}/>
            </div>
            {
                !taskScheduleStore.stateTaskContent ?
                    null
                    :
                    <div
                        className='home-task-content'>
                            <TaskContent/>
                    </div>
            }
        </div>
    )
})
