import {makeAutoObservable} from 'mobx'


interface IFormAuthRegStore {
    authValue: string
    authEmail: string
    authPass: string
    regValue: string
    regEmail: string
    regPass: string
    regPassConfirm: string
    errorStatus:boolean
}

class FormAuthRegStore implements IFormAuthRegStore {
    authValue = ''
    authEmail = ''
    authPass = ''
    regValue = ''
    regEmail = ''
    regPass = ''
    regPassConfirm = ''
    errorStatus = false


    constructor() {
        makeAutoObservable(this)
    }

    setAuthValue(val: string): void {
        this.authValue = val
    }

    setAuthEmail(val: string): void {
        this.authEmail = val
    }
    setAuthPass(val: string): void {
        this.authPass = val
    }

    setRegValue(val: string): void {
        this.regValue = val
    }

    setRegEmail(val: string): void {
        this.regEmail = val
    }
    setRegPass(val: string): void {
        this.regPass = val
    }
    setRegPassConfirm(val: string): void {
        this.regPassConfirm = val
    }
    setErrorStatus(val: boolean): void {
        this.errorStatus = val
    }

}

// eslint-disable-next-line import/no-anonymous-default-export
export default new FormAuthRegStore()
