import React, {useState} from 'react'
import {observer} from 'mobx-react-lite'
import DropdownSelectCategories from "../../../Dropdown/DropdownSelectCategories/DropdownSelectCategories";
import MyCalendar from "../../../Calendar/MyCalendar";
import taskContentStore from "../../../../../lib/store/task-content-store";
import DropdownTimers from "../../../Dropdown/DropdownTimers/DropdownTimers";
import taskScheduleStore from "../../../../../lib/store/task-schedule-store";


export default observer(() => {

    const [stateCheck, setStateCheck] = useState(false)
    return (
        <div className='task-content-header'>
            <div className='task-content-header-part-one'>
                <div className='task-content-header-part-one-checkbox'>
                    <input onChange={() => setStateCheck(!stateCheck)} type='checkbox' checked={stateCheck} className="checkbox checkbox-secondary"/>
                </div>
                <DropdownSelectCategories/>
            </div>
            <div className='task-content-header-part-two'>
                <MyCalendar/>
                <div className='task-content-header-part-two-date'>
                    {taskContentStore.date === taskScheduleStore.nowDate ? "Сегодня" : taskContentStore.date}
                </div>
                <DropdownTimers/>

            </div>
        </div>
    )
})
