import {makeAutoObservable} from 'mobx'


interface IDropdownTimers {
    value: string
    timers:string
}

class DropdownTimers implements IDropdownTimers {
    value = '00:00:00'
    timers = '00:00:00'


    constructor() {
        makeAutoObservable(this)
    }

    setValue(val: string): void {
        this.value = val
    }
    setTimers(val: string): void {
        this.timers = val
    }

}

// eslint-disable-next-line import/no-anonymous-default-export
export default new DropdownTimers()
