import React, {useEffect, useState} from 'react'
import {observer} from 'mobx-react-lite'
import dropdownTimerStore from "../../../../../lib/store/dropdown-timer-store";
import {countdown} from "../../../../../lib/helper";
// @ts-ignore
import pop from "../../../Music/pop.wav";
import taskContentStore from "../../../../../lib/store/task-content-store";
import addNotification from "react-push-notification";
// @ts-ignore
import logo from './logo192.jpg'

export default observer(() => {
    const [seconds, setSeconds] = useState(0);
    const [isActive, setIsActive] = useState(false);

    useEffect(() => {
        setSeconds(countdown(dropdownTimerStore.value))
    }, [dropdownTimerStore.value])

    function toggle() {
        if (seconds !== 0) {
            setIsActive(!isActive);
        }
    }

    function reset() {
        setSeconds(0);
        setIsActive(false);
        dropdownTimerStore.setValue('00:00:00')
        taskContentStore.setCheckbox(false)
    }

    useEffect(() => {
        let interval: any = null;
        if (isActive) {
            if (seconds !== 0) {
                interval = setInterval(() => {
                    setSeconds(seconds => seconds - 1);
                }, 1000);
            } else {
                clearInterval(interval)
                reset()
                new Audio(pop).play().then(r => console.log(r))
                addNotification({
                    title: 'task-schedule',
                    message: 'Время закончилось',
                    duration: 4000,
                    icon: logo,
                    native: true
                })
            }
        }
        return () => clearInterval(interval);
    }, [isActive, seconds]);


    function formatTime(time: number) {
        const hours = Math.floor(time / 3600);
        const minutes = Math.floor((time - hours * 3600) / 60);
        const seconds = time - hours * 3600 - minutes * 60;
        return `${hours.toString().padStart(2, '0')}:${minutes
            .toString()
            .padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
    }

    return (
        <div className='task-content-footer'>
            <div className='task-content-footer-timers'>{formatTime(seconds)}</div>
            <div>
                <button className='btn btn-primary task-content-footer-btn'
                        onClick={toggle}>{isActive ? 'Остановить' : 'Запустить'}</button>
                <button className='btn btn-primary-outline' onClick={reset}>Сбросить</button>
            </div>
        </div>
    )
})
