import searchFormStore from "./store/search-form-store";
import {Category} from "./models/category";
import taskScheduleStore from "./store/task-schedule-store";
import taskContentStore from "./store/task-content-store";


export const FilterVal = (Array: Category[]) => {
    // console.log(Array)
    return Array.filter((val) => {
        return val.task_title.toLowerCase().includes(searchFormStore.value.toLowerCase())
    })
}

export const countdown = (value: string): number => {
    const timeParts = value.split(":");
    return (+timeParts[0] * 60 * 60 + +timeParts[1] * 60 + +timeParts[2]);
}
export const validateEmail = (email: string) => {
    const pattern = /[^@ \t\r\n]+@[^@ \t\r\n]+\.[^@ \t\r\n]+/;
    return pattern.test(email);
};

export const changeStateTaskContent = (val: any): void => {

    taskScheduleStore.setStateTaskContent(true)
    taskContentStore.setTaskBody(val.task_body)
    taskContentStore.setTaskTitle(val.task_title)
    taskContentStore.setDate(val.date)
    taskContentStore.setTaskCategory(val.category)
}