import React, {ChangeEvent, useEffect} from 'react'
import {observer} from 'mobx-react-lite'
import dropdownTimerStore from "../../../../lib/store/dropdown-timer-store";
// @ts-ignore
import pop from "../../Music/pop.wav";
import taskContentStore from "../../../../lib/store/task-content-store";

// @ts-ignore
import iconTimer from '../../Imag/timer.svg'


export default observer(() => {
    const onChangeTime = (event: ChangeEvent<HTMLInputElement>): void => {
        if (event.target.value !== '') {
            dropdownTimerStore.setValue(event.target.value)
        }
    }

    const timer = (): void => {
        taskContentStore.setCheckbox(!taskContentStore.checkbox)
    }
    useEffect((): void => {
        if (taskContentStore.checkbox) {
            dropdownTimerStore.setValue("00:15:00")
        } else if (!taskContentStore.checkbox) {
            dropdownTimerStore.setValue("00:00:00")
        }
    }, [taskContentStore.checkbox])

    return (
        <div className='w-full'>
            <div className="dropdown  dropdown-end">
                <label tabIndex={1} className="btn-calendar btn btn-ghost m-2">
                    <img alt='calndar' className="icon-timer" src={iconTimer}/>
                </label>
                <div tabIndex={1} className="dropdown-content card card-compact w-64 p-2 shadow ">
                    <div className='dropdown-timers-deadline'>
                        <div>Дедлайн</div>

                        <input value={dropdownTimerStore.value} onChange={onChangeTime} type="time"
                               placeholder="Type here"
                               className="input input-time  " step={2}/>

                    </div>
                    <div className='dropdown-timers-deadline'>
                        Напомнить за 15 мин.
                        <input checked={taskContentStore.checkbox} onChange={timer}
                               type="checkbox"
                               className="toggle toggle-warning dropdown-timers-deadline-toggle"/>
                    </div>

                </div>
            </div>
        </div>
    )
})
